<template>
<div class="table-header-item">
  <div v-if="!headerObj.needOrder" class="nomarl">{{headerObj.name}}</div>
  <div v-if="headerObj.needOrder" :class="['nomarl', 'order-content']" @click="changeOrder">
    <!--<div :class="['label-content', headerObj.newLine ? 'smallLineHeigh' : '']">{{headerObj.name}}</div>-->
    <span :class="['label-content']">{{headerObj.name}}</span>
    <div class="imgs-content">
      <img :src="orderImg">
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'table-header-item',
  props: {
    headerObj: {
      type: Object,
      default: function () {
        return { name: '', needOrder: false, order: '0', prop: '', isPresent: false, needArrow: false, newLine: false }
        // name指展示的名称，needOrder标识是否需要排序，prop:标识字段名称， type： 0标识没有被选中，1标识升序， 2标识降序
        // newLine 当表头文字需要换行时设置为true
      }
    }
  },
  data () {
    return {
    }
  },
  computed: {
    orderImg () {
      let imgUrl = './images/noSelect@2x.png'
      if (this.headerObj.order === '0') {
        imgUrl = './images/noSelect@2x.png'
      } else if (this.headerObj.order === '1') {
        imgUrl = './images/upSelect@2x.png'
      } else if (this.headerObj.order === '2') {
        imgUrl = './images/downSelect@2x.png'
      }
      return imgUrl
    }
  },
  methods: {
    changeOrder () {
      return this.$emit('changeOrder', {header: this.headerObj})
    },
    getLineHeight (str) {
      if (str && str.length > 3) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .table-header-item {
    width: 100%;
    height: px2rem(80);
    .nomarl {
      width: 100%;
      height: px2rem(80);
      line-height: px2rem(80);
      font-size: px2rem(28);
      text-align: center;
      vertical-align: middle;
      color: #BBC5D2;
    }
    .order-content {
      display: flex;
      justify-content: center;
      align-items: center;
      .label-content {
        display: table-cell;
        width: auto;
        min-width: px2rem(80);
        text-align: center;
        vertical-align: middle;
        line-height: px2rem(24);
      }
      .smallLineHeigh {
        line-height: px2rem(40);
      }
      .imgs-content {
        width: px2rem(22);
        height: px2rem(24);
        line-height: px2rem(24);
        vertical-align: middle;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

</style>
