<template>
  <div class="min-title">
    <span class="title box-after">{{nameVal}} </span>
    <span class="title-unit" v-if="show === '0'">{{unit}}</span>
    <div class="btn-arr-content" v-if="show === '1'">
      <Tabs @tabsType = tabsType :tabsData="tabsData" :switchType="type" :defaultIndex="0"></Tabs>
    </div>
    <!--<div class="btn-arr2-content" v-if="show === '-1'">-->
      <!--<Tabs @tabsType = tabsType :tabsData="tabsData" :switchType="type" :defaultIndex="0"></Tabs>-->
    <!--</div>-->
  </div>
</template>

<script>
import Tabs from '../../components/common/tabs'
export default {
  name: 'MinTitle',
  props: {
    nameVal: {
      type: String,
      default: '总体概览'
    },
    show: {
      type: String,
      default: '0'
    },
    unit: {
      type: String,
      default: '单位：万元'
    },
    tabsData: {
      type: Array,
      default: () => [
        {
          name: '月',
          value: 'M'
        },
        {
          name: '年',
          value: 'Y'
        }
      ]
    },
    type: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      detailImg: './images/detail@2x.png'
    }
  },
  components: {
    Tabs
  },
  methods: {
    tabsType ({activeValue, index}) {
      this.$emit('tabsType', { 'activeValue': activeValue, 'name': this.nameVal, type: this.type, index })
    }
  }
}
</script>

<style lang="scss" scoped>
.min-title {
  height: px2rem(92);
  display: flex;
  justify-content: space-between;
  background: #1A2251;
  align-items: center;
  padding: px2rem(0) px2rem(30) px2rem(0) px2rem(30);
  .title {
    // border-left: px2rem(6) solid #8A99B6;
    font-size: px2rem(32);
    padding-left: px2rem(15);
    font-family: PingFangSC-Medium;
    .detail {
      width: px2rem(32);
      height: px2rem(32);
      vertical-align: middle;
      margin-left: px2rem(5);
    }
  }
  .box-after {
    position:relative;
  }
  .box-after::before {
    content: '';
    width: px2rem(6);
    height: px2rem(32);
    position: absolute;
    top: px2rem(4);
    left: 0;
    background: #8A99B6;
  }
  .title-unit {
    font-family: PingFangSC-Regular;
    font-size: px2rem(24);
    color: #BBC5D2;
  }
  .btn-arr-content {
    width: px2rem(320);
  }
  .btn-arr2-content{
     width: px2rem(200);
  }
}
</style>
