<template>
  <div class="tabs">
    <ul class="tabs-item">
      <li :class="index === activeIndex ? 'active' : ''" v-for="(item, index) in tabsData" :key="index" @click="changeTabs(index, item)" :style="withObj">{{item.name}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Title',
  props: {
    tabsData: {
      type: Array,
      default: () => [
        {
          name: '月',
          value: 'M'
        },
        {
          name: '年',
          value: 'Y'
        }
      ]
    },
    switchType: {
      type: String,
      default: ''
    },
    defaultIndex: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      activeIndex: 0,
      activeValue: ''
    }
  },
  computed: {
    withObj () {
      return { width: (100 / this.tabsData.length) + '%' }
    }
  },
  methods: {
    changeTabs (index, item) {
      this.activeIndex = index
      this.activeValue = item.value
      this.$emit('tabsType', {activeValue: this.activeValue, index: this.activeIndex, type: this.switchType})
    }
  },
  created () {
  }
}
</script>

<style lang="scss" scoped>
.tabs {
  .tabs-item {
    display: flex;
    align-items: center;
    width: 100%;
    height: px2rem(56);
    line-height: px2rem(56);
    font-size: px2rem(28);
    font-family: PingFangSC-Semibold;
    font-weight: bold;
    li {
      width: 50%;
      height: 100%;
      text-align: center;
      background: #0C1446;
      // border-radius: px2rem(8) 0 0 px2rem(8);
      color: #71768C;
      &:first-child {
        border-radius: px2rem(8) 0 0 px2rem(8);
      }
      &:last-child {
        border-radius: 0 px2rem(8) px2rem(8) 0;
      }
      &.active {
        background: #4C9AFF;
        color: #FFFFFF;
      }
    }
  }
}
</style>
