<template>
  <div class="table">
    <table class="table-list" cellspacing="0" cellpadding="0" bgcolor="#1A2251">
      <tr>
        <th v-for="(thItem, thIndex) in tableData.tableHeader" :key="thIndex">
          <table-header-item :headerObj="thItem" @changeOrder="changeOrder"></table-header-item>
        </th>
      </tr>
      <tr v-for="(rowItem, rowIndex) in tableData.tableContent" :key="rowIndex" @click="selectedTableRow(rowItem, rowIndex)" :class="[needClick && activeRowName === rowItem[tableData.tableHeader[0].prop] ? 'active-row': '', defaultSelected && activeRowName === rowItem[tableData.tableHeader[0].prop] ? 'active-row': '' ]">
        <td v-for="(header, propIndex) in tableData.tableHeader" :key="propIndex" :class="[header.needArrow && rowItem[header.prop] !== '--' ? isFuShu(rowItem[header.prop]) ? 'color-red' : 'color-green' : 'normal-color' ]">{{rowItem[header.prop]}}
          <span v-show="header.isPresent && rowItem[header.prop] !== '--'">%</span>
          <!--<span v-show="header.needArrow && !isFuShu(rowItem[header.prop]) && rowItem[header.prop] !== '&#45;&#45;'" >&uarr;</span>-->
          <!--<span v-show="header.needArrow && isFuShu(rowItem[header.prop]) && rowItem[header.prop] !== '&#45;&#45;'">&darr;</span>-->
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import TableHeaderItem from './tableHeaderItem'
export default {
  name: 'TableList',
  props: {
    tableData: {
      type: Object,
      default: () => ({tableHeader: [], tableContent: []})
    },
    type: {
      type: String,
      default: ''
    },
    needClick: {
      type: Boolean,
      default: false
    },
    defaultSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      activeRowName: ''
    }
  },
  computed: {
    selectedDwName () {
      return this.$store.state.selectedDwName
    },
    userIdentity () {
      return this.$store.state.userIdentity
    },
    selectedDpName () {
      return this.$store.state.selectedDpName
    }
  },
  methods: {
    isFuShu (val) {
      val = val + ''
      if (val.indexOf('-') > -1) {
        return true
      } else {
        return false
      }
    },
    changeOrder ({header}) {
      this.$emit('changeTableOrder', {header, type: this.type})
    },
    selectedTableRow (row, index) {
      if (this.needClick) {
        this.activeRowName = row[this.tableData.tableHeader[0].prop]
        this.$emit('changeTableRow', {row, name: this.activeRowName, type: this.type})
      }
    }
  },
  mounted () {
    if (this.needClick && this.tableData.tableContent.length > 0) {
      this.activeRowName = this.tableData.tableContent[0][this.tableData.tableHeader[0].prop]
    } else {
      this.activeRowName = ''
    }
    if (this.defaultSelected) {
      // 就把默认选中的名称传进来
      if (this.userIdentity === '1' || this.userIdentity === '2') {
        this.activeRowName = this.selectedDwName
      } else {
        this.activeRowName = this.selectedDpName
      }
    }
    this.$eventBus.$on('resetSelectedRow', ({type, name}) => {
      if (type === this.type) {
        this.activeRowName = name
      }
    })
  },
  watch: {
    tableData: {
      handler () {
        if (this.defaultSelected) {
          // 就把默认选中的名称传进来
          if (this.userIdentity === '1' || this.userIdentity === '2') {
            this.activeRowName = this.selectedDwName
          } else {
            this.activeRowName = this.selectedDpName
          }
        }
      },
      deep: true
    },
    selectedDwName: {
      handler (newData, oldData) {
        if (this.userIdentity === '1' || this.userIdentity === '2') {
          this.activeRowName = newData
        }
      }
    },
    selectedDpName: {
      handler (newData, oldData) {
        if (this.userIdentity === '3') {
          this.activeRowName = newData
        }
      }
    }
  },
  components: {
    TableHeaderItem
  }
}
</script>

<style lang="scss" scoped>
.table {
}
.table-list {
  width: 100%;
  tr {
    color: #fff;
    font-size: px2rem(26);
    text-align: center;
    th {
      max-width: px2rem(90);
      padding: 0 0;
      font-weight: normal;
      color: #BBC5D2;
      background: #0C1446;
      &:first-child {
        max-width: px2rem(120);
      }
    }
    td {
      padding: px2rem(24) px2rem(5);
      max-width: px2rem(90);
      word-wrap: break-word;
      &:first-child {
        max-width: px2rem(120);
      }
      // background: #1A2251;
    }
    .color-red {
      color: #FF435A;
    }
    .color-green {
      color: #8EC644;
    }
  }
  .active-row {
    background-color: #4D659C;
  }
}
</style>
